import { useState, useEffect, lazy, Suspense } from 'react';
import { useParams, Route, Link, useRouteMatch, NavLink } from 'react-router-dom';
import { fetchMovieDetailsById } from '../../services/movies-api';
import SearchError from '../SearchError/SearchError';
// import Cast from '../Cast/Cast';
// import Reviews from '../Reviews/Reviews';
import s from './MovieDetailsPage.module.css';

const Cast = lazy(() => import('../Cast/Cast' /* webpackChunkName: "cast" */));
const Reviews = lazy(() => import('../Reviews/Reviews' /* webpackChunkName: "reviews" */));
const Status = {
  IDLE: 'idle',
  RESOLVED: 'resolved',
  REJECTED: 'rejected',
};

export default function MovieDetailsPage() {
  const { movieId } = useParams();
  const { url, path } = useRouteMatch();
  const [movie, setMovie] =  useState(null);
  const [status, setStatus] = useState(Status.IDLE);
  const [error, setError] = useState(null);

  useEffect(() => {
    fetchMovieDetailsById(movieId)
    .then(result => {
      setMovie(result);
      setStatus(Status.RESOLVED);
    })
    .catch(error => {
      setError(error);
      setStatus(Status.REJECTED);
    });
  }, [movieId]);

  return (
    <>
    <Link className={s.goBack} to="/">Go back</Link>
    {status === Status.REJECTED && <SearchError message={error.message} />}
    {status === Status.RESOLVED &&
    <>
      <div className={s.movieInfo}>
        <img className={s.poster} src={`https://image.tmdb.org/t/p/original/${movie.poster_path}`} alt="Movie poster" width="250"/>
        <div>
          <h3>{movie.title}</h3>
          <p>User Score: %</p>
          <h4>Overview</h4>
          <p>{movie.overview}</p>
          <h5>Genres</h5>
          <ul>{movie.genres.map((genre) =>
            <li key={genre.id}>{genre.name}</li>
          )}</ul>
        </div>
      </div>
      <hr />

      <div>
        <p>Additional information</p>
        <ul>
          <li><NavLink to={`${url}/cast`}>Cast</NavLink></li>
          <li><NavLink to={`${url}/reviews`}>Reviews</NavLink></li>
        </ul>
      </div>
      <hr />
      </>
    }

    <Suspense fallback={<div>Loading...</div>}>
      <Route path={`${path}/cast`}>
        <Cast />
      </Route>
    </Suspense>

    <Suspense fallback={<div>Loading...</div>}>
      <Route path={`${path}/reviews`}>
        <Reviews />
      </Route>
    </Suspense>
    </>
  );
}